.otomatic-ai-app .tippy-box[data-animation="scale"][data-placement^="top"] {
    transform-origin: bottom;
}
.otomatic-ai-app .tippy-box[data-animation="scale"][data-placement^="bottom"] {
    transform-origin: top;
}
.otomatic-ai-app .tippy-box[data-animation="scale"][data-placement^="left"] {
    transform-origin: right;
}
.otomatic-ai-app .tippy-box[data-animation="scale"][data-placement^="right"] {
    transform-origin: left;
}
.otomatic-ai-app .tippy-box[data-animation="scale"][data-state="hidden"] {
    transform: scale(0.5);
    opacity: 0;
}
