<script setup>
import { ref, watch } from "vue";
import Multiselect from "@vueform/multiselect";

const props = defineProps({
    modelValue: {
        type: Array,
        default: [],
    },
    options: {
        type: [Array, Function],
        default: [],
    },
    mode: {
        type: String,
        default: "tags",
    },
    searchable: {
        type: Boolean,
        default: true,
    },
    createOption: {
        type: Boolean,
        default: true,
    },
    resolveOnLoad: {
        type: Boolean,
        default: true,
    },
    delay: {
        type: Number,
        default: -1,
    },
    minChars: {
        type: Number,
        default: 0,
    },
    allowAbsent: {
        type: Boolean,
        default: false,
    },
});

const emit = defineEmits(["update:modelValue"]);

const value = ref(props.modelValue);

watch(value, () => {
    emit("update:modelValue", value);
});
</script>

<template>
    <Multiselect
        class="text-sm"
        v-model="value"
        :options="options"
        :mode="mode"
        :searchable="searchable"
        :create-option="createOption"
        :resolve-on-load="resolveOnLoad"
        :delay="delay"
        :min-chars="minChars"
        :allow-absent="allowAbsent"
    />
</template>

<style lang="css">
.multiselect {
    --ms-bg: var(--color-gray-900);
    --ms-dropdown-bg: var(--color-gray-900);
    --ms-border-color: var(--color-gray-700);
    --ms-dropdown-border-color: var(--color-gray-700);
    --ms-tag-bg: var(--color-primary-500);
    --ms-empty-color: var(--color-gray-400);
    --ms-option-bg-pointed: var(--color-gray-800);
    --ms-option-color-pointed: var(--color-gray-200);
    --ms-spinner-color: var(--color-primary-500);
}
</style>
