<script setup>
import { ref, watch } from "vue";
import Datepicker from "@vuepic/vue-datepicker";
import { ClockIcon } from "@heroicons/vue/20/solid";

const props = defineProps({
    modelValue: {
        type: Object,
        default: () => ({
            hours: 0,
            minutes: 0,
        }),
    },
});

const emit = defineEmits(["update:modelValue"]);

const time = ref(props.modelValue);
const open = ref(false);

watch(time, () => {
    emit("update:modelValue", time.value);
});
</script>

<template>
    <Datepicker
        @open="open = true"
        @closed="open = false"
        v-model="time"
        :hide-input-icon="true"
        :clearable="false"
        position="center"
        teleport="#datepicker-container"
        :dark="true"
        class="relative"
        time-picker
    >
        <template #dp-input="{ value }">
            <button
                type="button"
                :class="[
                    'inline-flex w-full border text-gray-200 bg-gray-800 border-gray-700 items-center rounded-md shadow-sm placeholder:text-gray-500 justify-between text-sm px-3 py-2',
                    open ? 'border-primary-500 ring-1 ring-primary-500' : '',
                ]"
                aria-expanded="true"
                aria-haspopup="true"
            >
                {{ value }}
                <ClockIcon class="w-4 h-4 text-gray-400" aria-hidden="true" />
            </button>
        </template>
        <template #action-row="{ closePicker, selectDate }">
            <div class="flex items-center justify-between w-full">
                <button
                    type="button"
                    class="inline-flex items-center px-3 py-2 text-sm font-semibold text-gray-400 bg-gray-900 rounded-md hover:text-gray-200 hover:shadow-sm hover:bg-gray-800 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-primary-500"
                    @click="closePicker"
                >
                    Cancel
                </button>
                <button
                    type="button"
                    class="inline-flex items-center gap-x-1.5 rounded-md bg-gray-800 py-2 px-3 text-sm font-semibold text-gray-200 shadow-sm hover:bg-gray-700 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-primary-500"
                    @click="selectDate"
                >
                    Select
                </button>
            </div>
        </template>
    </Datepicker>
</template>

