/**
 * Remove wordpress content margin left
 */
.otomatic-ai-app {
    margin-left: -20px;
}

.otomatic-ai-app .min-h-wpcontent {
    min-height: calc(100vh - 32px);
}

.otomatic-ai-app .top-wpcontent {
    top: 32px;
}

.otomatic-ai-app .left-wpcontent {
    left: 36px;
}

@media screen and (max-width: 782px) {
    .otomatic-ai-app {
        margin-left: -10px;
        min-height: 800px;
    }

    .wp-responsive-open .otomatic-ai-app {
        margin-left: calc(190px - 16em - 10px);
    }

    .otomatic-ai-app .min-h-wpcontent {
        min-height: calc(100vh - 46px);
    }

    .otomatic-ai-app .top-wpcontent {
        top: 46px;
    }

    .otomatic-ai-app .left-wpcontent {
        left: 0px;
    }
}

/**
 * Remove wordpress default style
 */
.otomatic-ai-app input[type="date"],
.otomatic-ai-app input[type="datetime-local"],
.otomatic-ai-app input[type="datetime"],
.otomatic-ai-app input[type="email"],
.otomatic-ai-app input[type="month"],
.otomatic-ai-app input[type="number"],
.otomatic-ai-app input[type="password"],
.otomatic-ai-app input[type="search"],
.otomatic-ai-app input[type="tel"],
.otomatic-ai-app input[type="text"],
.otomatic-ai-app input[type="time"],
.otomatic-ai-app input[type="url"],
.otomatic-ai-app input[type="week"] {
    min-height: auto;
}

.otomatic-ai-app input[type="checkbox"]:checked::before {
    content: "";
}

.otomatic-ai-app input[type="radio"]::before {
    display: none;
}

.otomatic-ai-app dd,
.otomatic-ai-app li {
    margin-bottom: 0px;
}

.otomatic-ai-app a:focus {
    box-shadow: inherit;
    outline: inherit;
}

.otomatic-ai-app select {
    max-width: unset;
}
.otomatic-ai-app select:focus,
.otomatic-ai-app select:hover {
    color: unset;
}

.otomatic-ai-app input[type="checkbox"]:focus {
    border-color: unset;
}
