<script setup>
import { computed } from "vue";
import { useRoute, RouterView } from "vue-router";
import LiveChat from "@Components/LiveChat.vue";

const route = useRoute();
const layout = computed(() => {
    if (route.name) {
        return (route.meta.layout || "app") + "-layout";
    }
});
</script>

<template>
    <div class="fixed inset-0 w-screen h-screen overflow-auto z-[99999] font-sans text-gray-200 bg-gray-900 font-base">
        <div id="nprogress-container" class="sticky z-[100001] top-0"></div>
        <div id="datepicker-container"></div>
        <component v-if="layout" :is="layout">
            <RouterView />
        </component>
        <LiveChat />
    </div>
</template>

