<script setup>
import { ref, inject, onMounted, nextTick } from "vue";
import { useI18n } from "vue-i18n";
import Step from "@Components/Stepper/Step.vue";
import { useForm } from "@/form";
import LargeHelpButton from "@Components/HelpCenter/LargeHelpButton.vue";
import FormInput from "@Components/Form/Input.vue";
import SecondaryButton from "@Components/SecondaryButton.vue";
import LoadingAnimation from "@Components/Animation/Loading";
import { TrashIcon, ArrowPathIcon, Bars4Icon } from "@heroicons/vue/20/solid";
import _cloneDeep from "lodash/cloneDeep";
import { FormLabel } from "@Components/Form";
import PrimaryButton from "@Components/PrimaryButton.vue";
import { MagicIcon } from "@Icons/index";
import Draggable from "vuedraggable";
import { add } from "lodash";

const props = defineProps(["modelValue", "step"]);
defineEmits(["update:modelValue", "next", "back", "skip"]);

const { t: __ } = useI18n();
const requestsSettings = inject("requestsSettings");

const processing = ref(false);

const generateTitle = async (keyword) => {
    return await useForm({
        query: keyword,
        engine: props.modelValue.type,
        language: props.modelValue.language,
    }).post("requests_generate");
};

const generateStructure = async (title) => {
    return await useForm({
        title: title,
        language: props.modelValue.language,
        length: requestsSettings.value.length,
        h2_length: requestsSettings.value.h2_length,
        h3_length: requestsSettings.value.h3_length,
    }).post("structure_generate");
};

const generateH2 = async () => {
    if (processing.value) return;

    if (props.modelValue.requests[0].title.length === 0) {
        addSubtitle();
        return;
    }

    processing.value = true;

    let list = [];
    props.modelValue.modules.text.structure.forEach((h2) => {
        if (h2.title.length > 0) {
            list.push(h2.title);
        }
    });

    try {
        const response = await useForm({
            title: props.modelValue.requests[0].title,
            language: props.modelValue.language,
            length: requestsSettings.value.length,
            h2_list: list,
            h3_length: requestsSettings.value.h3_length,
        }).post("structure_generate_h2");

        if (response.title !== undefined) {
            props.modelValue.modules.text.structure.push({
                title: response.title,
                children: response.children !== undefined ? response.children : [],
            });
        }
    } catch (e) {}

    processing.value = false;
};

const generateH3 = async (indexH2) => {
    if (processing.value) return;

    if (props.modelValue.requests[0].title.length === 0) {
        addSubtitle(indexH2);
        return;
    }
    if (props.modelValue.modules.text.structure[indexH2].title.length === 0) return;

    processing.value = true;

    let list = [];
    props.modelValue.modules.text.structure[indexH2].children.forEach((h3) => {
        if (h3.title.length > 0) {
            list.push(h3.title);
        }
    });

    try {
        const response = await useForm({
            title: props.modelValue.requests[0].title,
            language: props.modelValue.language,
            h2_title: props.modelValue.modules.text.structure[indexH2].title,
            h3_list: list,
        }).post("structure_generate_h3");

        if (response.title !== undefined) {
            props.modelValue.modules.text.structure[indexH2].children.push({
                title: response.title,
            });
        }
    } catch (e) {}

    processing.value = false;
};

const refresh = async () => {
    if (processing.value) return;

    let keyword = undefined;
    if (props.modelValue.type === "url") {
        keyword = requestsSettings.value.url;
    } else {
        keyword = requestsSettings.value.keyword;
    }
    if (keyword === undefined || keyword.length === 0) return;

    processing.value = true;

    try {
        const title = await generateTitle(keyword);
        if (title !== undefined) {
            props.modelValue.requests = title.requests;

            if (props.modelValue.type !== "news" && props.modelValue.type !== "url") {
                props.modelValue.modules.text.structure = await generateStructure(props.modelValue.requests[0].title);
            }
        }
    } catch (e) {}

    processing.value = false;
};

const refreshStructure = async () => {
    if (processing.value) return;

    if (props.modelValue.requests[0].title.length === 0) return;

    processing.value = true;

    try {
        if (props.modelValue.type !== "news" && props.modelValue.type !== "url") {
            props.modelValue.modules.text.structure = await generateStructure(props.modelValue.requests[0].title);
        }
    } catch (e) {}

    processing.value = false;
};

const rewrite = async (title) => {
    if (processing.value) return;

    if (props.modelValue.requests[0].title.length === 0) return;

    processing.value = true;

    try {
        const { title } = await useForm({
            title: props.modelValue.requests[0].title,
            engine: props.modelValue.type,
            language: props.modelValue.language,
        }).post("requests_rewrite");

        if (title !== undefined && title.length > 0) {
            props.modelValue.requests[0].title = title;
        }
    } catch (e) {}

    processing.value = false;
};

const rewriteSubtitle = async (h2Index, h3Index) => {
    if (processing.value) return;

    let value = props.modelValue.modules.text.structure[h2Index].title;
    if (h3Index !== undefined) {
        value = props.modelValue.modules.text.structure[h2Index].children[h3Index].title;
    }

    if (value !== undefined && value.length > 0) {
        processing.value = true;

        try {
            const { title } = await useForm({
                title: value,
                engine: props.modelValue.type,
                language: props.modelValue.language,
            }).post("structure_rewrite");

            if (title !== undefined && title.length > 0) {
                if (h3Index === undefined) {
                    props.modelValue.modules.text.structure[h2Index].title = title;
                } else {
                    props.modelValue.modules.text.structure[h2Index].children[h3Index].title = title;
                }
            }
        } catch (e) {}

        processing.value = false;
    }
};

const addSubtitle = (h2Index) => {
    if (h2Index === undefined) {
        props.modelValue.modules.text.structure.push({
            title: "",
            children: [],
        });
    } else {
        props.modelValue.modules.text.structure[h2Index].children.push({
            title: "",
        });
    }
};

const removeSubtitle = (h2Index, h3Index) => {
    if (h3Index === undefined) {
        props.modelValue.modules.text.structure.splice(h2Index, 1);
    } else {
        props.modelValue.modules.text.structure[h2Index].children.splice(h3Index, 1);
    }
};

const itemsHistory = ref();
const onDragStart = (args) => {
    itemsHistory.value = _cloneDeep(props.modelValue.modules.text.structure);
};
const onDragEnd = (args) => {
    if (
        args.from.classList.contains("oto-h2-list") &&
        args.to.classList.contains("oto-h3-list") &&
        itemsHistory.value[args.oldDraggableIndex].children.length > 0
    ) {
        props.modelValue.modules.text.structure = _cloneDeep(itemsHistory.value);
    }
    itemsHistory.value = undefined;
};

if (props.modelValue.requests.length === 0) {
    props.modelValue.requests[0] = {
        title: "",
    };
}

// requestsSettings.value.keyword = "tomate";

const enterKeyPressed = (indexH2, indexH3) => {
    if (indexH3 === undefined) {
        props.modelValue.modules.text.structure.splice(indexH2 + 1, 0, {
            title: "",
            children: [],
        });
        nextTick(() => {
            let el = document.querySelector("[data-key='oto-input-" + (indexH2 + 1) + "']");
            el.focus();
        });
    } else {
        props.modelValue.modules.text.structure[indexH2].children.splice(indexH3 + 1, 0, {
            title: "",
        });
        nextTick(() => {
            let el = document.querySelector("[data-key='oto-input-" + indexH2 + "-" + (indexH3 + 1) + "']");
            el.focus();
        });
    }
};

// const updateTitle = ($event, h2Index) => {
//     if (h2Index === undefined) {
//     } else {
//         if
//         props.modelValue.modules.text.structure[h2Index].children.push({
//             title: "",
//         });
//     }
// };

onMounted(() => {
    if (props.modelValue.requests.length === 0 || props.modelValue.requests[0].title.length === 0) {
        refresh();
    }
});
</script>

<template>
    <Step class="px-4 sm:px-6 lg:px-8" :step="step" @next="$emit('next')" @back="$emit('back')" @skip="$emit('skip')">
        <template #header>
            <div class="max-w-3xl mx-auto">
                <div class="mb-3 text-center">
                    <LargeHelpButton size="large" category="generation" video-key="titles" />
                </div>
                <template v-if="modelValue.type === 'url'">
                    <h3 class="text-4xl font-semibold leading-tight text-center text-white">
                        {{ __("Modifiez votre titre") }}
                    </h3>
                </template>
                <template v-else>
                    <h3 class="text-4xl font-semibold leading-tight text-center text-white">
                        {{ __("Modifiez votre structure") }}
                    </h3>
                </template>
            </div>
        </template>
        <div class="relative flex flex-col max-w-5xl mx-auto space-y-6">
            <div>
                <FormLabel for="single-title">
                    {{ __("Titre de la publication") }}
                </FormLabel>

                <div class="flex items-center mt-2 space-x-3">
                    <div class="flex-grow">
                        <FormInput v-model="modelValue.requests[0].title" id="single-title"></FormInput>
                    </div>
                    <div class="flex flex-none space-x-1">
                        <SecondaryButton @click="rewrite">
                            <ArrowPathIcon class="w-4 h-4" />
                        </SecondaryButton>
                    </div>
                </div>
            </div>

            <div v-if="props.modelValue.type !== 'news' && props.modelValue.type !== 'url'" class="space-y-3">
                <div class="flex items-center space-x-3">
                    <div class="flex-1 block text-sm font-medium leading-6 text-white">
                        {{ __("Structure H2") }}
                    </div>
                    <div class="flex-none">
                        <PrimaryButton @click="refreshStructure()" class="gap-x-1.5 relative">
                            <MagicIcon class="w-4 h-4 shrink-0" />
                            {{ __("Regnrer") }}
                        </PrimaryButton>
                    </div>
                </div>
                <Draggable
                    :list="modelValue.modules.text.structure"
                    :group="'oto'"
                    handle=".oto-handle"
                    ghostClass="oto-ghost"
                    @start="onDragStart"
                    @end="onDragEnd"
                    class="space-y-6 oto-h2-list"
                >
                    <template #item="{ element: h2, index: indexH2 }">
                        <div>
                            <div class="flex items-center space-x-1">
                                <div class="flex-none">
                                    <Bars4Icon class="w-5 h-5 text-gray-400 cursor-pointer hover:text-white oto-handle" />
                                </div>
                                <div class="flex-none">
                                    <span class="bg-gray-800 px-1.5 py-1 rounded text-gray-200 font-bold">H2</span>
                                </div>
                                <div class="relative flex-grow">
                                    <FormInput
                                        v-model="modelValue.modules.text.structure[indexH2].title"
                                        @keydown.enter.prevent="enterKeyPressed(indexH2)"
                                        class="pr-4"
                                        :data-key="'oto-input-' + indexH2"
                                    />
                                    <div class="absolute inset-y-0 right-0 flex items-center mr-3 space-x-2">
                                        <ArrowPathIcon @click="rewriteSubtitle(indexH2)" class="w-4 h-4 text-gray-400 cursor-pointer hover:text-white" />
                                        <TrashIcon @click="removeSubtitle(indexH2)" class="w-4 h-4 text-gray-400 cursor-pointer hover:text-white" />
                                    </div>
                                </div>
                            </div>
                            <div class="mt-1 ml-8">
                                <Draggable :list="h2.children" :group="'oto'" handle=".oto-handle" ghostClass="oto-ghost" class="space-y-1 oto-h3-list">
                                    <template #item="{ element: h3, index: indexH3 }">
                                        <div class="flex items-center space-x-1">
                                            <div class="flex-none">
                                                <Bars4Icon class="w-5 h-5 text-gray-400 cursor-pointer hover:text-white oto-handle" />
                                            </div>
                                            <div class="flex-none">
                                                <span class="bg-gray-800 px-1.5 py-1 rounded text-gray-200 font-bold">H3</span>
                                            </div>
                                            <div class="relative flex-grow">
                                                <FormInput
                                                    v-model="modelValue.modules.text.structure[indexH2].children[indexH3].title"
                                                    @keydown.enter.prevent="enterKeyPressed(indexH2, indexH3)"
                                                    :data-key="'oto-input-' + indexH2 + '-' + indexH3"
                                                    class="pr-4"
                                                />
                                                <div class="absolute inset-y-0 right-0 flex items-center mr-3 space-x-2">
                                                    <ArrowPathIcon
                                                        @click="rewriteSubtitle(indexH2, indexH3)"
                                                        class="w-4 h-4 text-gray-400 cursor-pointer hover:text-white"
                                                    />
                                                    <TrashIcon
                                                        @click="removeSubtitle(indexH2, indexH3)"
                                                        class="w-4 h-4 text-gray-400 cursor-pointer hover:text-white"
                                                    />
                                                </div>
                                            </div>
                                        </div>
                                    </template>
                                    <template #footer>
                                        <div>
                                            <div class="relative mt-3">
                                                <div class="absolute inset-0 flex items-center" aria-hidden="true">
                                                    <div class="w-full border-t border-gray-700" />
                                                </div>
                                                <div class="relative flex justify-center">
                                                    <button
                                                        @click="generateH3(indexH2)"
                                                        type="button"
                                                        class="inline-flex items-center gap-x-1.5 rounded-full bg-gray-800 px-3 py-1.5 text-xs text-white shadow-sm ring-1 ring-inset ring-gray-700 hover:bg-gray-700"
                                                    >
                                                        <MagicIcon class="-ml-1 -mr-0.5 h-4 w-4 text-gray-200" aria-hidden="true" />
                                                        {{ props.modelValue.requests[0].title.length === 0 ? __("Ajouter un H3") : __("Gnrer un H3") }}
                                                    </button>
                                                </div>
                                            </div>
                                        </div>
                                    </template>
                                </Draggable>
                            </div>
                        </div>
                    </template>
                    <template #footer>
                        <div>
                            <div class="relative mt-6">
                                <div class="absolute inset-0 flex items-center" aria-hidden="true">
                                    <div class="w-full border-t border-gray-700" />
                                </div>
                                <div class="relative flex justify-center">
                                    <button
                                        @click="generateH2"
                                        type="button"
                                        class="inline-flex items-center gap-x-1.5 rounded-full bg-gray-800 px-3 py-1.5 text-xs text-white shadow-sm ring-1 ring-inset ring-gray-700 hover:bg-gray-700"
                                    >
                                        <MagicIcon class="-ml-1 -mr-0.5 h-4 w-4 text-gray-200" aria-hidden="true" />
                                        {{ props.modelValue.requests[0].title.length === 0 ? __("Ajouter un H2") : __("Gnrer un H2") }}
                                    </button>
                                </div>
                            </div>
                        </div>
                    </template>
                </Draggable>
            </div>

            <div v-if="processing" class="absolute inset-0 flex items-center justify-center bg-gray-900 bg-opacity-60">
                <LoadingAnimation class="w-16 h-16" />
            </div>
        </div>
    </Step>
</template>

<style>
.oto-ghost {
    border: 1px dashed #e2e8f0 !important;
    border-radius: 0.25rem !important;
}
.oto-ghost * {
    opacity: 0;
}
</style>
