<script setup>
import { ref, watch } from "vue";
import Slider from "@vueform/slider";
import FormInput from "./Input.vue";
import FormLabel from "./Label.vue";
import FormErrors from "./Errors.vue";
import _uniqueId from "lodash/uniqueId";

defineOptions({
    inheritAttrs: false,
});

const props = defineProps({
    modelValue: {
        type: Number,
        default: 0,
    },
    label: {
        type: String,
        default: undefined,
    },
    help: {
        type: String,
    },
    errors: {
        type: Array,
        default: [],
    },
    min: {
        type: Number,
        default: 0,
    },
    max: {
        type: Number,
        default: 100,
    },
    step: {
        type: Number,
        default: 1,
    },
});

const emit = defineEmits(["update:modelValue"]);

const key = _uniqueId("input-");

const value = ref(props.modelValue);

watch(value, () => {
    emit("update:modelValue", value.value);
});

watch(
    () => props.modelValue,
    () => {
        value.value = props.modelValue;
    }
);
</script>

<template>
    <div class="flex flex-col">
        <div class="flex items-center justify-between space-x-1.5">
            <FormLabel v-if="label" :for="key">
                {{ label }}
            </FormLabel>
            <input
                slider-input
                v-model.number="value"
                type="number"
                :id="key"
                :step="step"
                :min="min"
                :max="max"
                :class="[
                    'tabular-nums ml-auto w-[3.75rem] block focus:ring-primary-500 text-right rounded-md bg-transparent border-0 ring-0 py-1.5 text-gray-200 shadow-sm ring-inset  placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:bg-gray-900 text-sm leading-6',
                ]"
            />
        </div>

        <div class="mt-2">
            <Slider v-bind="$attrs" v-model="value" :min="min" :max="max" :step="step" :tooltips="false" :lazy="false" />
        </div>

        <div v-if="help" class="mt-2 text-xs text-gray-400">{{ help }}</div>

        <FormErrors v-if="errors" class="mt-2" :errors="errors" />
    </div>
</template>

<style lang="css">
.otomatic-ai-app .slider-target {
    --slider-handle-bg: var(--color-primary-500);
    --slider-bg: var(--color-gray-900);
    --slider-connect-bg: var(--color-primary-500);
}
</style>
