.otomatic-ai-app .slider-target,
.otomatic-ai-app .slider-target * {
    -webkit-touch-callout: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    box-sizing: border-box;
    touch-action: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
}
.otomatic-ai-app .slider-target {
    position: relative;
}
.otomatic-ai-app .slider-base,
.otomatic-ai-app .slider-connects {
    height: 100%;
    position: relative;
    width: 100%;
    z-index: 1;
}
.otomatic-ai-app .slider-connects {
    overflow: hidden;
    z-index: 0;
}
.otomatic-ai-app .slider-connect,
.otomatic-ai-app .slider-origin {
    height: 100%;
    position: absolute;
    right: 0;
    top: 0;
    -ms-transform-origin: 0 0;
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
    -webkit-transform-style: preserve-3d;
    transform-style: flat;
    width: 100%;
    will-change: transform;
    z-index: 1;
}
.otomatic-ai-app .slider-txt-dir-rtl.slider-horizontal .slider-origin {
    left: 0;
    right: auto;
}
.otomatic-ai-app .slider-vertical .slider-origin {
    top: -100%;
    width: 0;
}
.otomatic-ai-app .slider-horizontal .slider-origin {
    height: 0;
}
.otomatic-ai-app .slider-handle {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    position: absolute;
}
.otomatic-ai-app .slider-touch-area {
    height: 100%;
    width: 100%;
}
.otomatic-ai-app .slider-state-tap .slider-connect,
.otomatic-ai-app .slider-state-tap .slider-origin {
    transition: transform 0.3s;
}
.otomatic-ai-app .slider-state-drag * {
    cursor: inherit !important;
}
.otomatic-ai-app .slider-tooltip-drag .slider-tooltip,
.otomatic-ai-app .slider-tooltip-focus .slider-tooltip {
    display: none !important;
}
.otomatic-ai-app .slider-tooltip-drag .slider-active .slider-tooltip,
.otomatic-ai-app .slider-tooltip-drag.slider-state-drag .slider-tooltip:not(.slider-tooltip-hidden),
.otomatic-ai-app .slider-tooltip-focus.slider-focused .slider-tooltip:not(.slider-tooltip-hidden) {
    display: block !important;
}
.otomatic-ai-app .slider-horizontal {
    height: var(--slider-height, 6px);
}
.otomatic-ai-app .slider-horizontal .slider-handle {
    height: var(--slider-handle-height, 16px);
    right: calc(var(--slider-handle-width, 16px) / 2 * -1);
    top: calc((var(--slider-handle-height, 16px) - var(--slider-height, 6px)) / 2 * -1 + -1px);
    width: var(--slider-handle-width, 16px);
}
.otomatic-ai-app .slider-vertical {
    height: var(--slider-vertical-height, 300px);
    width: var(--slider-height, 6px);
}
.otomatic-ai-app .slider-vertical .slider-handle {
    bottom: calc(var(--slider-handle-width, 16px) / 2 * -1);
    height: var(--slider-handle-width, 16px);
    right: calc((var(--slider-handle-height, 16px) - var(--slider-height, 6px)) / 2 * -1 + -1px);
    width: var(--slider-handle-height, 16px);
}
.otomatic-ai-app .slider-txt-dir-rtl.slider-horizontal .slider-handle {
    left: calc(var(--slider-handle-width, 16px) / 2 * -1);
    right: auto;
}
.otomatic-ai-app .slider-base {
    background-color: var(--slider-bg, #d1d5db);
}
.otomatic-ai-app .slider-base,
.otomatic-ai-app .slider-connects {
    border-radius: var(--slider-radius, 9999px);
}
.otomatic-ai-app .slider-connect {
    background: var(--slider-connect-bg, #10b981);
    cursor: pointer;
}
.otomatic-ai-app .slider-draggable {
    cursor: ew-resize;
}
.otomatic-ai-app .slider-vertical .slider-draggable {
    cursor: ns-resize;
}
.otomatic-ai-app .slider-handle {
    background: var(--slider-handle-bg, #fff);
    border: var(--slider-handle-border, 0);
    border-radius: var(--slider-handle-radius, 9999px);
    cursor: -webkit-grab;
    cursor: grab;
    height: var(--slider-handle-height, 16px);
    width: var(--slider-handle-width, 16px);
}
.otomatic-ai-app .slider-handle:focus {
    outline: none;
}
.otomatic-ai-app .slider-active {
    cursor: -webkit-grabbing;
    cursor: grabbing;
}
.otomatic-ai-app [disabled] .slider-connect {
    background: var(--slider-connect-bg-disabled, #9ca3af);
}
.otomatic-ai-app [disabled] .slider-handle,
.otomatic-ai-app [disabled].slider-handle,
.otomatic-ai-app [disabled].slider-target {
    cursor: not-allowed;
}
.otomatic-ai-app [disabled] .slider-tooltip {
    background: var(--slider-tooltip-bg-disabled, #9ca3af);
    border-color: var(--slider-tooltip-bg-disabled, #9ca3af);
}
.otomatic-ai-app .slider-tooltip {
    background: var(--slider-tooltip-bg, #10b981);
    border: 1px solid var(--slider-tooltip-bg, #10b981);
    border-radius: var(--slider-tooltip-radius, 5px);
    color: var(--slider-tooltip-color, #fff);
    display: block;
    font-size: var(--slider-tooltip-font-size, 0.875rem);
    font-weight: var(--slider-tooltip-font-weight, 600);
    line-height: var(--slider-tooltip-line-height, 1.25rem);
    min-width: var(--slider-tooltip-min-width, 20px);
    padding: var(--slider-tooltip-py, 2px) var(--slider-tooltip-px, 6px);
    position: absolute;
    text-align: center;
    white-space: nowrap;
}
.otomatic-ai-app .slider-horizontal .slider-tooltip-top {
    bottom: calc(var(--slider-handle-height, 16px) + var(--slider-tooltip-arrow-size, 5px) + var(--slider-tooltip-distance, 3px));
    left: 50%;
    transform: translate(-50%);
}
.otomatic-ai-app .slider-horizontal .slider-tooltip-top:before {
    border: var(--slider-tooltip-arrow-size, 5px) solid transparent;
    border-top-color: inherit;
    bottom: calc(var(--slider-tooltip-arrow-size, 5px) * -2);
    content: "";
    height: 0;
    left: 50%;
    position: absolute;
    transform: translate(-50%);
    width: 0;
}
.otomatic-ai-app .slider-horizontal .slider-tooltip-bottom {
    left: 50%;
    top: calc(var(--slider-handle-height, 16px) + var(--slider-tooltip-arrow-size, 5px) + var(--slider-tooltip-distance, 3px));
    transform: translate(-50%);
}
.otomatic-ai-app .slider-horizontal .slider-tooltip-bottom:before {
    border: var(--slider-tooltip-arrow-size, 5px) solid transparent;
    border-bottom-color: inherit;
    content: "";
    height: 0;
    left: 50%;
    position: absolute;
    top: calc(var(--slider-tooltip-arrow-size, 5px) * -2);
    transform: translate(-50%);
    width: 0;
}
.otomatic-ai-app .slider-vertical .slider-tooltip-left {
    right: calc(var(--slider-handle-height, 16px) + var(--slider-tooltip-arrow-size, 5px) + var(--slider-tooltip-distance, 3px));
    top: 50%;
    transform: translateY(-50%);
}
.otomatic-ai-app .slider-vertical .slider-tooltip-left:before {
    border: var(--slider-tooltip-arrow-size, 5px) solid transparent;
    border-left-color: inherit;
    content: "";
    height: 0;
    position: absolute;
    right: calc(var(--slider-tooltip-arrow-size, 5px) * -2);
    top: 50%;
    transform: translateY(-50%);
    width: 0;
}
.otomatic-ai-app .slider-vertical .slider-tooltip-right {
    left: calc(var(--slider-handle-height, 16px) + var(--slider-tooltip-arrow-size, 5px) + var(--slider-tooltip-distance, 3px));
    top: 50%;
    transform: translateY(-50%);
}
.otomatic-ai-app .slider-vertical .slider-tooltip-right:before {
    border: var(--slider-tooltip-arrow-size, 5px) solid transparent;
    border-right-color: inherit;
    content: "";
    height: 0;
    left: calc(var(--slider-tooltip-arrow-size, 5px) * -2);
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 0;
}
.otomatic-ai-app .slider-horizontal .slider-origin > .slider-tooltip {
    left: auto;
    transform: translate(50%);
}
.otomatic-ai-app .slider-horizontal .slider-origin > .slider-tooltip-top {
    bottom: calc(
        var(--slider-tooltip-arrow-size, 5px) + (var(--slider-handle-height, 16px) - var(--slider-height, 6px)) / 2 + var(--slider-tooltip-distance, 3px) + 1px
    );
}
.otomatic-ai-app .slider-horizontal .slider-origin > .slider-tooltip-bottom {
    top: calc(
        var(--slider-tooltip-arrow-size, 5px) + (var(--slider-handle-height, 16px) - var(--slider-height, 6px)) / 2 + var(--slider-tooltip-distance, 3px) +
            var(--slider-height, 6px) - 1px
    );
}
.otomatic-ai-app .slider-vertical .slider-origin > .slider-tooltip {
    top: auto;
    transform: translateY(calc((var(--slider-tooltip-line-height, 1.25rem) - var(--slider-tooltip-py, 2px)) * -1 + 1px));
}
.otomatic-ai-app .slider-vertical .slider-origin > .slider-tooltip-left {
    right: calc(
        var(--slider-tooltip-arrow-size, 5px) + var(--slider-height, 6px) + (var(--slider-handle-height, 16px) - var(--slider-height, 6px)) / 2 +
            var(--slider-tooltip-distance, 3px) - 1px
    );
}
.otomatic-ai-app .slider-vertical .slider-origin > .slider-tooltip-right {
    left: calc(
        var(--slider-tooltip-arrow-size, 5px) + var(--slider-height, 6px) + (var(--slider-handle-height, 16px) - var(--slider-height, 6px)) / 2 +
            var(--slider-tooltip-distance, 3px) - var(--slider-height, 6px) + 1px
    );
}

.otomatic-ai-app [slider-input]::-webkit-outer-spin-button,
.otomatic-ai-app [slider-input]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.otomatic-ai-app [slider-input] {
    -moz-appearance: textfield;
}
