<script setup>
import { ref, watch } from "vue";
import Datepicker from "@vuepic/vue-datepicker";
import { ChevronDownIcon } from "@heroicons/vue/20/solid";

const props = defineProps({
    modelValue: {
        type: Date,
        default: 0,
    },
    minDate: {
        required: false,
    },
});

const emit = defineEmits(["update:modelValue"]);

const date = ref(props.modelValue);
const open = ref(false);

watch(date, () => {
    emit("update:modelValue", date.value);
});
</script>

<template>
    <Datepicker
        id="planning-until"
        @open="open = true"
        @closed="open = false"
        v-model="date"
        :hide-input-icon="true"
        :clearable="false"
        position="center"
        preventMinMaxNavigation
        :month-change-on-scroll="false"
        :enable-time-picker="false"
        teleport="#datepicker-container"
        :dark="true"
        class="relative"
    >
        <template #dp-input="{ value }">
            <button
                type="button"
                :class="[
                    'inline-flex w-full border text-gray-200 bg-gray-900 border-gray-700 rounded-md shadow-sm placeholder:text-gray-500 text-sm justify-center gap-x-1.5 px-3 py-2',
                    open ? 'border-primary-500 ring-1 ring-primary-500' : '',
                ]"
                aria-expanded="true"
                aria-haspopup="true"
            >
                {{ value }}
                <ChevronDownIcon class="w-5 h-5 -mr-1 text-gray-400" aria-hidden="true" />
            </button>
        </template>
        <template #action-row="{ closePicker, selectDate }">
            <div class="flex items-center justify-between w-full">
                <button
                    type="button"
                    class="inline-flex items-center px-3 py-2 text-sm font-semibold text-gray-400 bg-gray-900 rounded-md hover:text-gray-200 hover:shadow-sm hover:bg-gray-800 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-primary-500"
                    @click="closePicker"
                >
                    Annuler
                </button>
                <button
                    type="button"
                    class="inline-flex items-center gap-x-1.5 rounded-md bg-gray-800 py-2 px-3 text-sm font-semibold text-gray-200 shadow-sm hover:bg-gray-700 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-primary-500"
                    @click="selectDate"
                >
                    Valider
                </button>
            </div>
        </template>
    </Datepicker>
</template>

